import React, { useEffect, useState } from 'react';

import {
  Grid
} from '@material-ui/core'

import { colors } from '../../../styles/theme'

import {
  DynamicAbility,
  StaticAbility
} from '../../../components/Builds'

const FragmentBuildAbilities = (props) => {
  const {
    data,
    setData
  } = props

  // Hacking a state change :(
  const [count, setCount] = useState(0)

  const selectLevel = (abilityIndex, level) => {
    let tempData = data

    tempData.map((_, index) => {
      tempData[index].levels = tempData[index].levels.filter(i => i !== level)
    })

    tempData[abilityIndex].levels.push(level)
    setData(tempData)
    setCount(count+1)
  }

  return (
    <Grid container 
      alignContent='center'
      alignItems='center'>
        <Grid item xs={12}>
          <Grid container>
            <Grid item xs={6}>
              { data && data[0] && 
              <StaticAbility ability={data[0]} />
              }
            </Grid>
            <Grid item xs={6}>
              { data && data[1] &&
              <StaticAbility ability={data[1]} />
              }
            </Grid>
          </Grid>
        </Grid>
        <Grid item xs={12}>
          <Grid container>
            { data && data.slice(2, 6).map((ability, index) =>
             <Grid item xs={12} style={styles.dynamicAbilities}
                key={`ability-${index}`}>
                <DynamicAbility ability={ability} abilityIndex={index+2} selectLevel={selectLevel} />
            </Grid>
            )}
          </Grid>
        </Grid> 
    </Grid>
  )
}

export default FragmentBuildAbilities;

const styles = {
  header: {
    color: colors.gray
  },
  dynamicAbilities: {
    marginTop: 35,
    marginBottom: 10
  }
}