import CreateBuild from '../pages/builds/Create'
import ViewBuild from '../pages/builds/View'
import NotFound from '../pages/NotFound'

const routes = [
  { path: "", key: "ROOT", exact: true, component: CreateBuild },
  { path: "/", key: "ROOT-slash", exact: true, component: CreateBuild },
  { path: "/etherean-builds-ghp", key: "ROOT-ghp", exact: true, component: CreateBuild },

  // Builds
  { path: "/builds/create", key: "CREATE_BUILD", exact: true, component: CreateBuild },
  { path: "/builds/view/:buildId", key: "VIEW_BUILD", component: ViewBuild },

  // Catch All Else
  { path: "/:NotFound", component: NotFound }
];

export default routes;